import { Link } from 'react-router-dom';
import { useParams } from "react-router-dom";
import parse from "html-react-parser";
import 'materialize-css/dist/css/materialize.min.css';
import Logo from './logocrow.png';
import Data from './data/posts/posts.json';
import './App.css';

const Homepage = () => {
	return (
		<div>
				<Header/>
				<Banner/>
				<Mainpost/>
				<Postlist/>
		</div>
	)
};

function Mainpost() {
  const mypost = Data[Data.length - 1];
	console.log(mypost);
  return (
    <div>
    <div class='maincard'>
          <div>
					<Link to={`/post/${mypost.id}`}>
					<div class='card'>
						<div class="card-image">
							<img src={mypost.bigimage} alt='{mypost.id}'/>
						</div>
						 <div class="card-content">
								<h5>{mypost.name}</h5>
								<p class='maincard'>{mypost.desc}</p>
						 </div>
					</div>
					</Link>
          </div>
    </div>
    </div>
  )
}

const Postlist = () => {
	return (
    <div class='mycontent row'>
      { Data.map((postdetail,i)=>{
        return <div class='col s12 l4'>
                 <Link to={`/post/${postdetail.id}`}>
								 <div class='card'>
                   <div class="card-image">
                     <img src={postdetail.image} alt='{postdetail.id}'/>
                   </div>
										<div class="card-content">
											 <h5>{postdetail.name}</h5>
											 <p class='maincard'>{postdetail.desc}</p>
										</div>
                 </div>
								 </Link>
               </div>
            })}
    </div>
	)
};

function Checkpost() {
  const { id } = useParams();
  let num = Object.values({id})[0];
  const mypost = Data[num];
  return (
    <div>

    <Header/>
		<div class='banner'>
			{mypost.date}
    </div>
    <div class='mycontent'>
				<nav class='white z-depth-0 hide-on-med-and-down'>
						{mypost.sections.map((parts,j)=> (
							<Checkcrumb crumbtext={parts.title} />
						))}
				</nav>
        <h4>{mypost.name}</h4>
        {mypost.sections.map((parts,j)=> (
          <div>
            <h5 id={parts.title}>{parts.title} </h5>
            <Checkpic pictext={parts.image} />
            <p class='content'>{parse(parts.content)}</p>
            <Checklink linktext={parts}/>
          </div>
        ))}
    </div>
    </div>
  )
}

function Checkcrumb(crumbtext) {
  let crumb = crumbtext.crumbtext;
  if (crumb !== "") {
		let myid = "#" + crumb;
    return <a href={myid} class="breadcrumb">{crumb}</a>;
  }
  return "";
}

function Checkpic(pictext) {
  let pic = pictext.pictext;
  if (pic !== "") {
    return <img src={pic} width='100%' alt='pictext.pictext'/>;
  }
  return "";
}

function Checklink(linktext) {
  let link = linktext.linktext.link;
	let top = linktext.linktext.hr;
	console.log(linktext,link,top);
  if (link !== "" && top !== "") {
    return <div><a href={link} target='_blank' rel='noreferrer'>More info</a> / <a href='#top'>Top</a><hr class='myhr'/></div>;
  }
	if (link === "" && top !== "") {
		return <div><a href='#top'>Top</a><hr class='myhr'/></div>;
	}

  return "";
}

const Header = () => {
	return (
    <div class='header'>
			<Link to='/'><img src={Logo} class='logo' alt='crow logo'/></Link>
    </div>
	)
};

const Banner = () => {
	return (
    <div class='banner'>
          #whatif #alternatehistory #forelooker
    </div>
	)
};

export {Homepage, Header, Checkpost} ;
